body {
  background-color: #ddd;
  margin: 0px;
  padding: 0px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#score-divider {
  margin-left: 10px;
  margin-right: 10px;
}

.scoreboard {
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: #333;
  color: #eee;
  margin-bottom: 20px;
}

.among {
  width: 65px;
  height: 65px;
}

.among-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  justify-content: center;
  align-content: center;
  align-items: center;
  gap: 10px;
  width: 1000px;
}

.among-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  border: 1px solid black;
  width: 150px;
  height: 150px;
  background-color: #eee;
  cursor: pointer;
}

.winner-wrapper {
  text-align: center;
  margin-top: 20px;
}
